<template>
	<header class="header">
		<div class="container">
			<div class="row">
				<div class="col-md-12 col-sm-12">
					<img src="../assets/images/tm-easy-profile.jpg" class="img-responsive img-circle tm-border" alt="templatemo easy profile">
					<hr>
					<h1 class="tm-title bold shadow">Hi, I am {{name}}</h1>
					<h1 class="white bold shadow">{{job}}</h1>
				</div>
			</div>
		</div>
	</header>	
</template>

<script>
export default {
  name: 'MyHeader',
data () {
    return {
      name: 'Julia',
      job:'WEB'
    }
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



